header {
    width: 100%;
    height: 100%;
    padding-top: 7rem;
    padding-bottom: 6rem;
    object-fit: cover;

    /* Animate Background Image */
  
    .title-header span {
      color: whitesmoke;
    }
    h1 {
      letter-spacing: 0;
      padding: 0.25em 0 0.325em;
      display: block;
      color: white;
      font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
      
      /* Clip Background Image */
      background: url(../assets/animated-text-1-min.webp) repeat-y;
      -webkit-background-clip: text;
      background-clip: text;
  
      /* Animate Background Image */
      -webkit-text-fill-color: transparent;
      animation: aitf 65s linear infinite;
  
      /* Activate hardware acceleration for smoother animations */
      transform: translate3d(0, 0, 0);
      backface-visibility: hidden;
    }
    .title-header {
      font-size: 2rem;
      text-align: center;
      padding-bottom: 4rem;
      font-family: "Space Grotesk", sans-serif;
    }
    h2 {
      padding-top: 2rem;
      text-transform: uppercase;
      color: #61dafb;
      font-size: 2rem;
      text-align: center;
    }
    .btn-header {
      display: flex;
      align-items: center;
      justify-content: space-around;
      margin-top: 10rem;
      gap: 4rem;
    }
    a {
      transform: translate(0, 0);
      text-transform: uppercase;
      color: #ff6943;
      padding: 20px 30px;
      font-size: 20px;
      font-family: sans-serif;
      letter-spacing: 1px;
      text-decoration: none;
      box-shadow: 0 20px 50px rgba(68, 52, 126, 0.05);
      overflow: hidden;
      background-color: #171011;
      &:before {
        position: absolute;
        top: 2px;
        left: 2px;
        bottom: 2px;
        content: "";
        width: 100%;
        background: rgba(255, 255, 255, 0.05);
        overflow: hidden;
      }
      &::before {
        content: "";
        position: absolute;
        top: 60px;
        left: 0;
        width: 100%;
        height: 100%;
        background: #ff6943;
        border-radius: 50% 50% 0% 0%;
        z-index: -1;
        transition: all 0.5s;
      }
      &:hover::before {
        top: 0px;
        border-radius: 0;
      }
      span {
        &:nth-child(1) {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 3px;
          background: #ff6943;
          animation: animate1 2s linear infinite;
        }
        &:nth-child(2) {
          position: absolute;
          top: 0;
          right: 0;
          width: 3px;
          height: 100%;
          background: #ff6943;
          animation: animate2 2s linear infinite;
          animation-delay: 1s;
        }
  
        &:nth-child(3) {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 3px;
          background: #ff6943;
          animation: animate3 2s linear infinite;
        }
  
        &:nth-child(4) {
          position: absolute;
          top: 0;
          left: 0;
          width: 3px;
          height: 100%;
          background: #ff6943;
          animation: animate4 2s linear infinite;
          animation-delay: 1s;
        }
      }
      &:hover {
        font-weight: 400;
        color: #171011;
      }
    }
  }
  @keyframes aitf {
    0% {
      background-position: 0% 20%;
    }
  
    100% {
      background-position: 90% 20%;
    }
  }
  @keyframes animate1 {
    0% {
      transform: translateX(-100%);
    }
  
    100% {
      transform: translateX(100%);
    }
  }
  @keyframes animate2 {
    0% {
      transform: translateY(-100%);
    }
  
    100% {
      transform: translateY(100%);
    }
  }
  @keyframes animate3 {
    0% {
      transform: translateX(100%);
    }
  
    100% {
      transform: translateX(-100%);
    }
  }
  @keyframes animate4 {
    0% {
      transform: translateY(100%);
    }
  
    100% {
      transform: translateY(-100%);
    }
  }
  @media screen and (max-width: 940px) {
    header {
      .btn-header {
        display: flex;
        flex-direction: column;
        margin-bottom: 6rem;
        max-width: 100%;
      }
      h1 {
        font-size: 2.8rem;
      }
      img {
        height: 100%;
      }
    }
  }
  @media screen and (max-width: 1368px) {
    video {
      width: 100%;
      object-fit: cover;
    }
  }


