#services {
    display: flex;
    flex-direction: column;
    color: white;
    text-align: center;
    align-items: center;
    h1,
    h2 {
        font-size: 2rem;
    }
    .services__container {
        display: grid;
        width: max-content;
        grid-template-columns: 3fr 3fr 3fr;
        margin-top: 2rem;
        .service {
            max-width: 20rem;
            height: 20rem;
            margin: 1rem;
            background-color: #171011;
            border: solid 1px #ff6943;
            padding: 15px 20px;
            font-size: 1.4rem;
            position: relative;
        }
        img {
            position: absolute;
            top: 35%;
            left: 25%;
            width: 50%;
        }
        @media screen and (max-width: 1053px) {
            grid-template-columns: 3fr 3fr;
        }
        @media screen and (max-width: 702px) {
            grid-template-columns: 3fr;
        }
        @media screen and (max-width: 352px) {
            display: flex;
            flex-direction: column;
            max-width: 100%;
            img {
                top: 40%;
            }
        }
    }
}
