$color_1: white;
$color_2: #ff6943;
$font-family_1: sans-serif;
$border-bottom-color_1: #ddd;

.contact {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 91vh;
	margin-top: 3rem;
	font-size: 1.5rem;
	font-family: $font-family_1;
	h1 {
		margin-top: 5px;
		color: $color_1;
		text-align: center;
		margin-bottom: 1rem;
	}
}
.background {
	margin-top: 1rem;
	margin-bottom: 3.2rem;
	min-width: 50rem;
}
.screen {
	display: flex;
	flex-direction: column;
	position: relative;
	background: #171011;
	border-radius: 15px;
	&:after {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 20px;
		right: 20px;
		bottom: 0;
		border-radius: 15px;
		box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
		z-index: -1;
		overflow: hidden;
	}
}
.screen-header {
	display: flex;
	align-items: center;
	padding: 10px 20px;
	background: #ff6943;
	border-top-left-radius: 15px;
	border-top-right-radius: 15px;
	overflow: hidden;
}
.screen-header-left {
	margin-right: auto;
}
.screen-header-button {
	display: inline-block;
	width: 8px;
	height: 8px;
	margin-right: 3px;
	border-radius: 8px;
	background: white;
}
.screen-header-button.close {
	background: red;
}
.screen-header-button.maximize {
	background: #e8e925;
}
.screen-header-button.minimize {
	background: #74c54f;
}
.screen-header-right {
	display: flex;
}
.screen-header-ellipsis {
	width: 3px;
	height: 3px;
	margin-left: 2px;
	border-radius: 8px;
	background: #999;
}
.screen-body {
	display: flex;
}
.screen-body-item {
	flex: 1;
	padding: 50px;
}
.screen-body-item.left {
	display: flex;
	flex-direction: column;
}
.app-title {
	display: flex;
	flex-direction: column;
	position: relative;
	color: $color_2;
	font-size: 26px;
	&:after {
		content: "";
		display: block;
		position: absolute;
		left: 0;
		bottom: -10px;
		width: 25px;
		height: 4px;
		background: #ff6943;
	}
}
.app-contact {
	margin-top: auto;
	font-size: 1rem;
	color: $color_2;
	a {
		color: $color_2;
		&:hover {
			color: $color_1;
			border-bottom: solid 1px #ff6943;
		}
	}
}
.app-form-group {
	margin-bottom: 15px;
}
.app-form-group.message {
	margin-top: 40px;
}
.app-form-group.buttons {
	margin-bottom: 0;
	text-align: right;
}
.app-form-control {
	width: 100%;
	padding: 10px 0;
	background: none;
	border: none;
	border-bottom: 1px solid #ff6943;
	color: $color_1;
	font-size: 14px;
	text-transform: uppercase;
	outline: none;
	transition: border-color 0.2s;
	&::placeholder {
		color: $color_2;
		opacity: 0.5;
	}
	&:focus {
		border-bottom-color: $border-bottom-color_1;
	}
}
.app-form-button {
	background: none;
	border: none;
	color: $color_2;
	font-size: 14px;
	cursor: pointer;
	outline: none;
	padding: 1rem;
	&:hover {
		color: $color_1;
	}
}
@media screen and (max-width: 809px) {
	.background {
		min-width: 100%;
	}
}
@media screen and (max-width: 520px) {
	.contact {
		padding-top: 4rem;
	}
	.screen-body {
		flex-direction: column;
	}
	.screen-body-item.left {
		margin-bottom: 30px;
	}
	.app-title {
		flex-direction: row;
		span {
			margin-right: 12px;
		}
		&:after {
			display: none;
		}
	}
}
@media screen and (max-width: 600px) {
	.screen-body {
		padding: 40px;
	}
	.screen-body-item {
		padding: 0;
	}
}
