$color_4: #61dafb;
$color_5: white;
$color_6: #ff6943;
$font-family_5: "Source Sans Pro", sans-serif;
$font-family_6: sans-serif;
$background-color_5: #171011;

.projects {
	width: 100%;
	height: 100%;
	padding-top: 1rem;
	padding-bottom: 1rem;
	.portfolio {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		width: 100%;
		height: 100%;
		max-width: 100%;
	}
	.portfolio2 {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		width: 100%;
		height: 100%;
		max-width: 100%;
	}
	h1 {
		text-align: center;
		color: white;
		font-size: 2rem;
		// padding: 1rem;
	}
	h2 {
		margin-bottom: 1rem;
	}
	.card {
		border-radius: 15px;
		overflow: hidden;
		margin: 10px;
		width: 300px;
		height: auto;
		border: solid 2px #ff6943;
		background-color: $background-color_5;
		box-shadow: 0px 2px 10px #ff6943;
		transition: 0.5s;
		&:hover {
			transform: scale(1.1);
		}
		a {
			color: $color_4;
			text-decoration: none;
		}
	}
	.card-image {
		height: 155px;
		position: relative;
		overflow: hidden;
		// border: solid 2px black;
		img {
			width: 150%;
			position: absolute;
			top: 106%;
			left: 50%;
			background-position: center;
			transform: translate(-50%, -35%);
			transition-property: filter width;
			transition-duration: 0.3s;
			transition-timing-function: ease;
		}
	}
	.card-image0 {
		img {
			position: absolute;
			top: 105%;
			left: 74%;
		}
	}
	.card-image2 {
		img {
			top: 90%;
		}
	}
	.card-image3 {
		img {
			top: 63%;
		}
	}
	.card-image4 {
		img {
			top: 50%;
		}
	}
	.card-image5 {
		img {
			top: 51%;
			width: 100%;
		}
	}
	.card-image6 {
		img {
			top: 60%;
		}
	}
	.card-image7 {
		img {
			top: 38%;
			width: 100%;
		}
	}
	.card-body {
		border-top: solid 1px #ff6943;
		text-align: center;
		padding: 15px 20px;
		box-sizing: border-box;
		height: 240px;
	}
	.card-date {
		font-family: $font-family_5;
		text-align: center;
		text-transform: uppercase;
		font-weight: bold;
		font-family: $font-family_6;
		color: $color_5;
		color: $color_6;
	}
	.card-title {
		font-family: $font-family_6;
		text-align: center;
		text-transform: uppercase;
		font-weight: bold;
		font-family: $font-family_6;
		color: $color_5;
	}
	.card-excerpt {
		font-family: $font-family_6;
		color: $color_6;
	}
}
@media screen and (max-width: 290px) {
	.projects {
		padding-left: 1rem;
	}
}