.skills {
  display: flex;
  margin-top: 6rem;
  padding-left: 2%;
  padding-right: 2%;
  border: solid 1pw white;
  aside {
    height: 100%;
    display: flex;
    background-size: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    img {
      height: 100%;
    }
    p {
      font-family: sans-serif;
      font-weight: 400;
      font-size: 1.1rem;
      text-align: center;
      color: #ff6943;
    }
    img {
      width: 50%;
      height: auto;
      border-radius: 20px;
      object-fit: cover;
      transition: 0.7s;
      &:hover {
        transform: scale(1.1);
      }
    }
  }
}
.skill {
  border-radius: 20px;
  height: 100%;
  background-color: #171011;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-left: 2rem;
  margin-right: 2rem;
  border: solid 1px #ff6943;
  .languageDisplay {
    display: flex;
    flex-direction: column;
    color: white;
    font-family: sans-serif;
    font-size: 1.1em;
    font-weight: 500;
    .languagesList {
      padding: 10px;
      // text-align: center;
      border-radius: 50px;
      margin: 15px 0;
    }
  }
  .progressBar {
    height: 0.4rem;
    background: transparent;
    border-radius: 50px;
    margin-top: 0.2rem;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      background: #ff6943;
      border-radius: 50px;
      height: 0.8rem;
      width: 0;
      animation: line 2s ease-out forwards;
      @keyframes line {
        to {
          width: 100%;
        }
      }
    }
  }
}
.skills {
  aside {
    border-radius: 20px;
    width: 100%;
    height: 100%;
    background-color: #171011;
    padding: 1rem;
    border: solid 1px #ff6943;
  }
}
@media screen and (max-width: 1030px) {
  .skills {
    flex-direction: column;
    aside {
      margin-top: 2rem;
      margin-bottom: 2rem;
      img {
        margin-top: 2rem;
      }
    }
    .skill {
      margin-left: 0;
    }
  }
}
