.navbar {
    background-color: #171011;
    border-bottom: 1px solid #ff6943;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 20;
    .nav-container {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 80px;
      max-width: 1500px;
      a img {
        border: solid 2px #ff6943;
        border-radius: 50%;
        width: 4rem;
        height: 100%;
      }
    }
    .nav-logo {
      color: white;
      display: flex;
      align-items: center;
      margin-left: 20px;
      cursor: pointer;
      font-size: 2rem;
      flex-grow: 1;
    }
    .nav-menu {
      display: flex;
      text-align: center;
      margin-right: 2rem;
    }
    .nav-links {
      color: #ff6943;
      text-decoration: none;
      padding: 0.5rem 1rem;
      margin-left: 1.5rem;
      height: 100%;
      border-radius: 5px;
      border: 1px solid #ff6943;
      transition: 0.5s;
      font-family: sans-serif;
      
      &:hover {
        background-color: #ff6943;
        color: #171011;
      }
    }
    .nav-item .active
    {
      padding: 0.5rem 1rem;
      margin-left: 1.5rem;
      height: 100%;
      border-radius: 5px;
      border: 1px solid #ff6943;
      transition: 0.5s;
      font-family: sans-serif;
      color: #171011;
      background-color: #ff6943;
    }
    
    .nav-icon {
      display: none;
    }
  }
  @media screen and (max-width: 960px) {
    .navbar {
      .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 80px;
        left: -110%;
        padding: 1rem;
        opacity: 1;
        transition: all 0.5s ease;
        &.active {
          background: #171011;
          left: 0px;
          opacity: 1;
          transition: all 0.5s ease;
          z-index: 1;
        }
      }
      .nav-item .active {
        margin-left: 0;
        margin-bottom: 15px;
        padding: 1.5rem;
        width: 100%;
        display: table;
        color: black;
        border: none;
      }
      .nav-links {
        margin-left: 0;
        margin-bottom: 15px;
        padding: 1.5rem;
        width: 100%;
        display: table;
      }
      .nav-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
        color: #ff6943;
      }
    }
  }
