* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
    outline: none;
    border: none;
    text-decoration: none;
}
body {
    background-color: #282c34;
    margin: 0;
    padding: 0;
    height: 100%;
    video {
        position: fixed;
        z-index: -15;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    p {
        font-family: sans-serif;
    }
}
section {
    margin-top: 1rem;
}
#root {
    height: 100%;
}
h1,
h2 {
    font-family: cursive;
    color: white;
    text-align: center;
}
